.qarr-field {
  margin-bottom: 15px;
}

.qarr-field input[type="text"], .qarr-field input[type="url"], .qarr-field textarea {
  display: block;
  width: 100%;
  height: 100%;
  color: black;
  background-color: transparent;
  box-sizing: border-box;
  border: none;
  border-radius: 5px;
  outline: none;
  font-family: "OpenSans-Regular", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: normal;
  padding: 27px 40px 5px 16px;
  margin: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  resize: none;
  transition: all .3s ease;
}

.qarr-field.qarr-custom-select select {
  height: auto;
  padding: 14px 16px;
}

.qarr-field select {
  width: 100%;
  height: 51px;
  border: 2px solid #E9EFF4;
  padding: 0 16px;
  border-radius: 5px;
  color: black;
}

.qarr-field .qarr-label {
  display: block;
  font-family: "OpenSans-SemiBold", "Helvetica", sans-serif;
  font-size: 14px;
  margin-bottom: 5px;
}

.qarr-field .multiselect select {
  height: auto;
  padding: 10px;
}

.qarr-field.custom-field {
  background: #fff;
  position: relative;
  border: 2px solid #E9EFF4;
  border-radius: 6px;
  transition: all .3s ease;
  min-height: 52px;
}

.qarr-field.custom-field:before {
  content: '';
  position: absolute;
  bottom: -2px;
  right: -2px;
  border-style: solid;
  border-width: 0 0 0 0;
  z-index: 4;
  border-color: #fff #fff;
  transition: all .3s ease .35s;
}

.qarr-field.custom-field.has-value input + label {
  transform: translate(0, -4px) scale(0.72);
}

.qarr-field.custom-field.has-value input + label + .qarr-input-icon {
  opacity: 1;
}

.qarr-field.custom-field.has-value textarea + label {
  transform: translate(0, -10px) scale(0.72);
}

.qarr-field.custom-field.has-value textarea + label + .qarr-input-icon {
  opacity: 1;
}

.qarr-field.custom-field.has-value .select2-container + label {
  transform: translate(0, -5px) scale(0.72);
}

.qarr-field.custom-field.has-value .select2-container + label + .qarr-input-icon {
  opacity: 1;
}

.qarr-field.custom-field.has-focus {
  border-color: #B289EF;
}

.qarr-field.custom-field.has-error {
  animation: qarr-shake 0.6s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  border-color: #f07575;
}

.qarr-field.custom-field.has-error label {
  color: #f07575;
}

.qarr-field.custom-field.has-error:before {
  border-width: 38px 32px 0 0;
  border-color: #f07575 #fff;
  border-radius: 6px 0 0 0;
  transition-delay: 0s;
}

.qarr-field.custom-field.has-error .qarr-error-icon {
  color: #f07575;
  opacity: 1;
  transform: scale(1);
  transition: transform 0.4s cubic-bezier(0.87, -0.41, 0.19, 1.44) 0.35s, opacity 0.3s ease 0.45s;
}

.qarr-field.custom-field input:focus + label {
  transform: translate(0, -4px) scale(0.72);
}

.qarr-field.custom-field input:focus + label + .qarr-input-icon {
  opacity: 1;
}

.qarr-field.custom-field textarea + label {
  top: 30px;
}

.qarr-field.custom-field textarea:focus + label {
  transform: translate(0, -10px) scale(0.72);
}

.qarr-field.custom-field textarea:focus + label + .qarr-input-icon {
  opacity: 1;
}

.qarr-field.custom-field label {
  position: absolute;
  z-index: 1;
  left: 16px;
  top: 50%;
  line-height: 24px;
  font-family: "OpenSans-Regular", "Helvetica", sans-serif;
  font-weight: 600;
  font-size: 12px;
  letter-spacing: .05em;
  color: #a5a6a8;
  margin: -12px 0 0 0;
  cursor: text;
  transform-origin: top left;
  transition: all .3s ease;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}

.qarr-field.custom-field .qarr-input-icon {
  position: absolute;
  z-index: 2;
  stroke: none;
  fill: #3F4656;
  display: block;
  right: 16px;
  top: 50%;
  opacity: .2;
  cursor: pointer;
  transform: translate(0, -50%);
  transition: opacity .4s ease;
}

.qarr-field.custom-field .qarr-input-icon svg {
  width: 14px;
  height: 14px;
}

.qarr-field.custom-field .qarr-error-icon {
  position: absolute;
  right: -1px;
  bottom: -5px;
  display: block;
  opacity: 0;
  stroke: none;
  z-index: 5;
  fill: #EB3E60;
  transform: scale(0.4);
  transition: all .3s ease;
}

.qarr-field.custom-field .qarr-error-icon svg {
  width: 14px;
  height: 14px;
}

.qarr-field .checkbox-group input, .qarr-field .checkbox-group label, .qarr-field .radio-group input, .qarr-field .radio-group label {
  margin: 0 5px 0 0;
}

.qarr-field .checkbox-group div, .qarr-field .radio-group div {
  position: relative;
  display: flex;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 10px;
}

.qarr-field .checkbox-group div label, .qarr-field .radio-group div label {
  font-family: "OpenSans-SemiBold", "Helvetica", sans-serif;
  font-size: 12px;
  position: relative;
  padding-left: 35px;
}

.qarr-field .checkbox-group div label:before, .qarr-field .checkbox-group div label:after, .qarr-field .radio-group div label:before, .qarr-field .radio-group div label:after {
  content: '';
  display: block;
  position: absolute;
  top: -2px;
  left: 0;
  width: 24px;
  height: 24px;
  box-sizing: border-box;
}

.qarr-field .checkbox-group div label:before, .qarr-field .radio-group div label:before {
  border: 2px solid #E9EFF4;
  border-radius: 5px;
  transition: all .3s ease;
}

.qarr-field .checkbox-group div label:after, .qarr-field .radio-group div label:after {
  opacity: 0;
  width: 16px;
  height: 16px;
  top: 2px;
  left: 4px;
  background: #B289EF;
  border-radius: 2px;
  transition: opacity .3s ease;
}

.qarr-field .checkbox-group div label.selected:after, .qarr-field .radio-group div label.selected:after {
  opacity: 1;
}

.qarr-field .checkbox-group div input[type="checkbox"], .qarr-field .checkbox-group div input[type="radio"], .qarr-field .radio-group div input[type="checkbox"], .qarr-field .radio-group div input[type="radio"] {
  display: none;
}

.qarr-field .checkbox-group div input[type="checkbox"]:checked + label:after, .qarr-field .checkbox-group div input[type="radio"]:checked + label:after, .qarr-field .radio-group div input[type="checkbox"]:checked + label:after, .qarr-field .radio-group div input[type="radio"]:checked + label:after {
  opacity: 1;
}

.qarr-field .radio-group label:before, .qarr-field .radio-group label:after {
