/*
* ===========================================
* QARR Entries Pagination
* ===========================================
*
*/
.qarr-pagination {
  display: flex;
  margin-top: 20px;
  padding-top: 20px;
}

.qarr-pagination .qarr-pager-container {
  display: flex;
}

.qarr-pagination .qarr-pager-container .qarr-pager {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  margin-right: 10px;
  font-family: "OpenSans-Bold", "Helvetica", sans-serif;
  font-size: 16px;
  line-height: normal;
  border: 1px solid #E9EFF4;
  border-radius: 5px;
  color: #474747;
  transition: background .3s ease;
}

.qarr-pagination .qarr-pager-container .qarr-pager:hover {
  background: #E9EFF4;
}

.qarr-pagination .qarr-pager-container .qarr-pager.current-pager {
  background: #E9EFF4;
  color: #4a7294;
}

.qarr-pagination .qarr-pager-container .qarr-pager.pager-disabled {
  pointer-events: none;
  opacity: .5;
}

.qarr-pagination .qarr-pager-container .qarr-pager.qarr-pager-infinite {
  width: auto;
  height: auto;
  padding: 10px 20px;
  font-size: 12px;
}

@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(19px, 0);
  }
}

@keyframes qarr-shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }
  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }
  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }
  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}



