.qarr-lightswitch {
    position: relative;
    border: none !important;
    overflow: hidden;
    cursor: pointer;
    user-select: none;
    transition: box-shadow linear 100ms;

    &:not(.small) {
        border-radius: 11px;
        width: 33px;
        height: 22px;

        .lightswitch-container {
            margin-left: -11px;
            width: 44px;
        }

        .label {
            width: 22px;
            height: 22px;

            &.on {
                border-radius: 11px 0 0 11px;
            }
            &.off {
                border-radius: 0 11px 11px 0;
            }
        }

        .handle {
            border-radius: 10px;
            margin: 1px -10px;
            width: 20px;
            height: 20px;
        }
    }

    &.small {
        border-radius: 9px;
        width: 27px;
        height: 18px;

        .lightswitch-container {
            margin-left: -9px;
            width: 36px;
        }

        .label {
            width: 18px;
            height: 18px;

            &.on {
                border-radius: 9px 0 0 9px;
            }
            &.off {
                border-radius: 0 9px 9px 0;
            }
        }

        .handle {
            border-radius: 8px;
            margin: 1px -8px;
            width: 16px;
            height: 16px;
        }
    }

    table & {
        display: inline-block;
        margin-bottom: -5px;
    }

    &:focus {
        outline: none;
    }

    &.on {
        .lightswitch-container {
            margin-left: 0;
        }
    }

    .label {
        float: left;
        box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1);
    }

    .label.on,
    &:not(.dragging).on .label.off {
        background: $c-purple;
    }

    .label.off,
    &:not(.dragging):not(.on) .label.on {
        background: $c-border;
    }

    .handle {
        position: relative;
        float: left;
        background: white;
        box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1);
    }
}


