@mixin clearafter {
    content: '.';
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}

.hud {
    position: absolute;
    display: none;
    top: 0;

    .hud-name {
        display: flex;
        font-family: $font-semiBold;
        font-size: 12px;
        margin-bottom: 20px;

        .qarr-lightswitch {
            margin-left: auto;
        }
    }

    .qarr-field {
        margin: 0;
    }

    .qarr-footer {
        display: flex;
        border-top: 1px solid $c-border;
        transition: opacity .3s ease;
        padding: 0 !important;

        & > input {
            flex: 1;
            font-family: $font-bold;
            font-size: 10px;
            text-transform: uppercase;
            letter-spacing: .05em;
            line-height: normal;
            cursor: pointer;
            transition: all .3s ease;
            border-radius: 0;

            &:first-child {
                border-right: 1px solid $c-border;
            }

            &:hover {
                background: $c-border;
            }
        }

        .btn-modal.submit {
            margin-right: -5px;
        }

    }
    
    // Buttons
    input[type="button"], input[type="submit"] {
        display: block;
        outline: none;
        border: none;
        padding: 18px 0;
        margin: 0;
        height: 100%;
        background: white;
        cursor: pointer;
        -webkit-appearance: none;
        -moz-appearance: none;
    }

    &.has-footer .tip-bottom {
        background-size: 100%;
        background-image: url('data:image/png;base64,');
    }

    .tip {
        position: absolute;
        z-index: 101;
        background: no-repeat 0 0;
    }

    .tip-left {
        left: -15px;
        width: 15px;
        height: 30px;
        background-size: 100%;
        background-image: url('data:image/png;base64,');
    }

    .tip-top {
        top: -15px;
        width: 30px;
        height: 15px;
        background-size: 100%;
        background-image: url('data:image/png;base64,');
    }

    .tip-right {
        right: -15px;
        width: 15px;
        height: 30px;
        background-size: 100%;
        background-image: url('data:image/png;base64,');
    }

    .tip-bottom {
        bottom: -15px;
        width: 30px;
        height: 15px;
        background-size: 100%;
        background-image: url('data:image/png;base64,');
    }

    .qarr-hub-success-message {
        span {
            display: block;
            font-family: $font-regular;
            font-size: 12px;
            line-height: normal;
            color: $c-gray;
            @include antialias;

            &:first-child {
                font-family: $font-bold;
                padding-bottom: 4px;
            }
        }
    }
}

.hud {
    z-index: 100;
    box-sizing: border-box;
}

.hud .body {
    border-radius: 5px;
    background: #fff;
    box-shadow: 0 25px 100px rgba(0, 0, 0, 0.5);
}

.hud-header,
.hud-footer {
    &:after {
        @include clearafter;
    }
}
.hud-header {
    border-radius: 5px 5px 0 0;
    padding: 24px;
    background: $c-border;

    h1 {
        margin: 0;
    }
}

.hud-footer {
    border-radius: 0 0 5px 5px;
    background: $c-border;
}

.hud .main {
    padding: 24px;
    overflow: hidden;
    box-sizing: border-box;
}

.hud-shade {
    z-index: 100;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
}

.hud .hud-header {
    padding: 7px 24px;
}

.hud .body {
    overflow: hidden;

    ::-webkit-scrollbar {
        appearance: none;

        &:vertical {
            width: 11px;
        }

        &:horizontal {
            height: 11px;
        }
    }

    ::-webkit-scrollbar-thumb {
        border-radius: 8px;
        border: 2px solid transparent;
        background-color: rgba(0, 0, 0, .5);
        background-clip: content-box;
    }

    ::-webkit-scrollbar-track {
        background-color: $c-border;
    }
}


